<script>
import { ref, onMounted, onUnmounted } from "vue"
import { monthHandle } from "@/utils/handle"
import NoData from "@/components/global/NoData.vue"
import { apiGetWorkList } from "@/api/api"
import { isBetweenBottom } from "@/utils/helper"
import { onBeforeRouteLeave } from "vue-router"

import SearchWork from "./components/SearchWork.vue"

import BackToTop from "@/components/global/BackToTop.vue"
import SaveWindowY from "@/components/global/SaveWindowY.vue"

export default {
  name: "workList_",
  setup() {
    const workListData = ref([])
    const limit = 10

    const page = ref(1)
    let totalData = 0
    let getApiTimer = null

    onMounted(async () => {
      workListData.value = await apiGetWorkList({ page: 1, limit })
      totalData = workListData.value.data.total
      workListData.value = workListData.value.data.list
      dataHandle(workListData.value)
      //每0.5S監測是否在底部
      getApiTimer = setInterval(() => {
        if (!isBetweenBottom()) return
        if (workListData.value?.length < totalData) {
          handleScroll()
        }
      }, 500)
    })

    //處理資料
    const dataHandle = (data) => {
      if (data.length <= 0) return []
      data.forEach((item) => {
        let res = monthHandle(item.workdays)
        item.newDates = res.newDates
        item.info = res.info
        item.time = res.time
        item.monthInfo = Object.keys(item.info)
        item.diseaseList = []
        item.disease.forEach((x) => item.diseaseList.push(x.diseaseName))
      })
    }
    //滾動載入資料
    const handleScroll = async () => {
      page.value += 1
      let newData = ref()
      newData.value = await apiGetWorkList({ page: page.value, limit })
      workListData.value = workListData.value.concat(newData.value.data.list)
      dataHandle(workListData.value)
    }

    onUnmounted(() => clearInterval(getApiTimer))

    const updateParams = async (val) => {
      page.value = 1
      const params = { ...val, page: page.value, limit }
      const response = await apiGetWorkList(params)

      if (response.status) {
        const newData = response.data.list || []
        totalData = response.data.total
        workListData.value = newData
        dataHandle(workListData.value)
      }
    }
    return { workListData, updateParams }
  },
  components: {
    NoData,
    SearchWork,
    BackToTop,
    SaveWindowY,
  },
}
</script>

<template>
  <div class="workList">
    <SaveWindowY />
    <section class="c-main">
      <SearchWork @params="updateParams" />
      <div class="work-container">
        <NoData v-if="workListData?.length <= 0" />
        <div v-if="workListData?.length">
          <div
            class="card"
            v-for="item in workListData"
            :key="item.workOrderId"
          >
            <div class="card-header">
              <a
                @click="
                  $router.push({ path: `/work/info/${item.workOrderId}` })
                "
                class="card-link"
              >
                <div class="card-title">
                  <i
                    class="far fa-mars"
                    v-if="item.disabledPerson.gender == 'Male'"
                  ></i
                  ><i
                    class="far fa-venus"
                    v-if="item.disabledPerson.gender == 'Female'"
                  ></i>
                </div>
                <div class="card-item">
                  <div class="btn btn-item">{{ item.hours }}小時</div>
                  <div class="btn btn-item">
                    {{ item.servicePlace.placeType.name }}
                  </div>
                </div>
              </a>
            </div>
            <div
              @click="$router.push({ path: `/work/info/${item.workOrderId}` })"
              class="card-body"
            >
              <a class="card-link">
                <div class="card-date" v-if="item.monthInfo">
                  <div class="d-flex">
                    <div class="col-left">照顧日期：</div>
                    <div class="col-right">
                      {{ item?.monthInfo?.[0] }}/{{
                        item?.info?.[item?.monthInfo?.[0]].join("、")
                      }}
                    </div>
                  </div>
                  <div class="d-flex" v-if="item.monthInfo.length == 2">
                    <div class="col-left"></div>
                    <div class="col-right">
                      {{ item?.monthInfo?.[1] }}/{{
                        item?.info?.[item?.monthInfo?.[1]].join("、")
                      }}
                    </div>
                  </div>
                </div>
                <div class="card-time">服務時段：{{ item.time }}</div>
                <!-- place -->
                <div
                  class="card-location"
                  v-if="item.servicePlace.placeType.name == '居家'"
                >
                  <span v-if="item.servicePlace">
                    服務地點：{{ item.servicePlace.city.name
                    }}{{ item.servicePlace.area.name }}
                  </span>
                  <span v-if="!item.servicePlace"> 服務地點：無 </span>
                </div>
                <div
                  class="card-location"
                  v-if="item.servicePlace.placeType.name != '居家'"
                >
                  <span v-if="item.servicePlace">
                    服務地點：{{ item.servicePlace.place.name }}
                  </span>
                  <span v-if="!item.servicePlace"> 服務地點：無 </span>
                </div>
                <!--  -->
                <div class="card-info">
                  <span v-if="item.disabledPerson">
                    被照顧者資訊：{{ item.disabledPerson.age }}歲/{{
                      item.disabledPerson.height
                    }}cm/{{ item.disabledPerson.weight }}kg</span
                  >
                  <span v-if="!item.disabledPerson">被照顧者資訊：無</span>
                </div>
                <div class="card-disease">
                  <span v-if="item.diseaseList"
                    >被照顧者病症：{{ item.diseaseList.join("、") }}</span
                  >
                  <span v-if="!item.diseaseList">被照顧者病症：無</span>
                </div>
              </a>
            </div>
          </div>
        </div>
      </div>
    </section>
    <BackToTop />
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.workList {
  margin-top: $header-height;
  // margin-bottom: calc($footer-height + 15px);
}
</style>
