<script>
import { ref, watch, onMounted, computed, getCurrentInstance } from "vue"
import CitySelecter from "@/components/global/CitySelecter.vue"
import { Toast } from "@/components/global/swal"
import { onBeforeRouteLeave } from "vue-router"
import { useJobStore } from "@/store/global"
import { apiGetJobType } from "@/api/api"

export default {
  props: {},
  setup(props, { emit }) {
    const jobStore = useJobStore()
    const jobTypeList = ref({})

    //篩選popup
    let showPopup = ref("") //顯示popup的類別
    const isGreatSearch = ref(false)

    const btnSwitch = (popMode = "") => {
      showPopup.value !== popMode
        ? (showPopup.value = popMode)
        : (showPopup.value = "")
    }

    const applyKeywordSearch = async () => {
      emit("params", params.value)
      closePop()
    }

    const defaultData = {
      page: 1,
      limit: 10,
      keyword: "",
      orderBy: "updatedDate",
      jobTypeId: [],
      cityId: undefined,
      areaId: undefined,
      orderType: "DESC",
    }
    let params = ref(JSON.parse(JSON.stringify(defaultData)))

    const updateCityIdx = (val) => {
      params.value.cityId = val.value
      params.value.areaId = undefined
    }
    const updateAreaIdx = (val) => {
      params.value.areaId = val.value
    }

    const closePop = () => {
      showPopup.value = ""
    }

    const clearAllParam = () => {
      const oldValue = JSON.stringify(defaultData)
      const newValue = JSON.stringify(params.value)
      if (newValue === oldValue) {
        Toast("請更新搜尋條件")
      } else {
        params.value = JSON.parse(JSON.stringify(defaultData))
        params.value.cityId = undefined
        params.value.areaId = undefined
        console.log(params)
        emit("params", params.value)
      }
    }

    onBeforeRouteLeave((to, from, next) => {
      document.body.style.overflow = "auto"
      next()
    })
    onMounted(async () => {
      //取工作類別
      if (jobStore.jobTypeList) {
        jobTypeList.value = jobStore.jobTypeList
      } else {
        jobTypeList.value = await apiGetJobType()
        jobTypeList.value = jobTypeList.value.data.data
        jobStore.jobTypeList = jobTypeList.value
      }
    })
    watch(
      () => showPopup.value,
      () => {
        if (showPopup.value !== "") {
          document.body.style.overflow = "hidden"
        } else {
          document.body.style.overflow = "auto"
        }
      }
    )

    // const searchAreaClass = ref()
    const handleGreatSearchClick = () => {
      isGreatSearch.value = !isGreatSearch.value
    }

    const searchAreaClass = computed(() => {
      let ans = "search-area h-25"
      if (showPopup.value == "search") {
        ans = isGreatSearch.value ? "search-area h-49" : "search-area h-25"
      } else if (showPopup.value == "sort") {
        ans = "search-area h-22"
      }
      return ans
    })

    const sortType = [
      {
        id: "ASC",
        name: "ASC",
        showText: "遞增(由小到大)",
      },
      {
        id: "DESC",
        name: "DESC",
        showText: "遞減(由大到小)",
      },
    ]
    const sortingData = [
      {
        id: "updatedDate",
        name: "更新時間",
      },
      {
        id: "createdDate",
        name: "建立時間",
      },
    ]
    const closePopPrevent = (val) => {
      if (val === 1) {
        closePop()
      }
    }
    return {
      params,
      showPopup,
      btnSwitch,

      updateCityIdx,
      updateAreaIdx,

      applyKeywordSearch,
      closePop,
      closePopPrevent,

      clearAllParam,
      isGreatSearch,
      handleGreatSearchClick,
      searchAreaClass,

      sortingData,
      sortType,

      jobTypeList,
    }
  },
  components: { CitySelecter },
}
</script>

<template>
  <div>
    <div class="ui-tab">
      <div class="row">
        <div class="col">
          <a @click="btnSwitch('search')"><i class="far fa-search"></i>搜尋</a>
        </div>
        <div class="col">
          <a @click="btnSwitch('sort')"
            ><i class="far fa-sort-amount-down"></i>排序</a
          >
        </div>
        <div class="col">
          <a v-if="showPopup" @click="closePop"
            ><i class="far fa-window-close"></i>關閉</a
          >
          <a v-if="!showPopup" @click="clearAllParam"
            ><i class="far fa-window-close"></i>清除條件</a
          >
        </div>
      </div>
    </div>
    <div class="popup" v-if="showPopup != ''" @click="closePopPrevent(1)">
      <div :class="searchAreaClass" @click.stop="closePopPrevent(0)">
        <!-- sort start -->
        <div v-if="showPopup == 'search'">
          <div class="row d-flex justify-content-between">
            <div class="col-5">
              <label class="form-label">請輸入關鍵字</label>
            </div>
            <div class="col text-end">
              <!-- <a @click="clearAllParam"
                ><i class="far fa-window-close"></i
                ><span class="custom-filter-text me-2">清除條件</span></a
              > -->
              <a @click="handleGreatSearchClick"
                ><i class="far fa-filter"></i
                ><span class="custom-filter-text">{{
                  isGreatSearch ? "精簡" : "進階"
                }}</span></a
              >
            </div>
          </div>
          <div class="mb-3">
            <input
              type="text"
              class="form-control mt-1"
              placeholder="ex: 心臟病"
              v-model="params.keyword"
            />
          </div>

          <div v-if="isGreatSearch">
            <div class="mb-3">
              <label class="form-label">縣市<span class="star"></span></label>
              <CitySelecter
                @city-idx="updateCityIdx"
                @area-idx="updateAreaIdx"
                :cityIdx_p="params.cityId"
                :areaIdx_p="params.areaId"
              />
            </div>
            <div>
              <label class="form-label">服務類別(可複選)</label>
              <div class="input-list">
                <div
                  class="form-check-inline"
                  v-for="jobItem in jobTypeList"
                  :key="jobItem.id"
                >
                  <input
                    type="checkbox"
                    class="form-check-input"
                    :value="jobItem.id"
                    :id="`job${jobItem.id}`"
                    v-model="params.jobTypeId"
                  />
                  <label class="form-check-label" :for="`job${jobItem.id}`">{{
                    jobItem.name
                  }}</label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- sort end -->
        <!-- sort start -->
        <div v-if="showPopup == 'sort'">
          <div class="container">
            <div class="input-pill">
              <div
                class="row d-flex justify-content-center align-center text-center"
              >
                <div
                  class="form-check col"
                  v-for="item in sortType"
                  :key="item.id"
                >
                  <input
                    type="radio"
                    :value="item.name"
                    :id="item.id"
                    v-model="params.orderType"
                    class="custom-radio"
                  />
                  <label :for="item.id" class="custom-filter-text">
                    {{ item.showText }}
                  </label>
                </div>
              </div>
              <div class="row">
                <div
                  class="col-4 form-check"
                  v-for="sortItem in sortingData"
                  :key="sortItem.id"
                >
                  <input
                    type="radio"
                    class="form-check-input"
                    :value="sortItem.id"
                    :id="sortItem.id"
                    v-model="params.orderBy"
                  />
                  <label class="form-check-label" :for="sortItem.id">{{
                    sortItem.name
                  }}</label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- sort end -->
        <!-- btn start -->
        <div class="sticky mt-4b btn-container searchBtn">
          <div class="row mb-2">
            <div class="col">
              <button
                class="btn btn-secondary"
                type="button"
                @click="applyKeywordSearch"
              >
                套用
              </button>
            </div>
          </div>
        </div>
        <!-- btn end -->
      </div>
      <div>剩下的高度</div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.custom-filter-text {
  font-size: 0.9rem;
}

.h-22 {
  height: 22% !important;
  max-height: 250px;
}
.h-49 {
  height: 49% !important;
  max-height: 600px;
}

.sticky {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.95);
  // opacity: 0.5;
}
.search-area {
  position: fixed;
  background-color: #ffffff;
  z-index: 2;
  width: 100%;
  border-radius: 0 0 1rem 1rem;
  top: calc($header-height + 32.5px);
  border-top: 1px solid #e8e8e8;
  padding: 0.75rem;
  overflow: scroll !important;
}

.popup {
  position: fixed;
  background-color: rgba(211, 211, 211, 0.8);
  z-index: 2;
  width: 100%;
  height: 100%;
}

.searchBtn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  button {
    width: 5rem;
  }
}
.custom-radio {
  accent-color: rgb(142, 142, 142);
}
.form-check-label {
  border-radius: 0.25rem;
}
</style>
